* {
  margin: 0;
  padding: 0;
  color: white
}

html {
  height: 100%;
  background: radial-gradient(#1b2735 0%, #090a0f 100%);
  overflow-x: hidden;
}

/* Header/Nav */

header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 10px 20px;
  margin-bottom: 5%;
}

header h1 {
  font-size: 4vw;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin-top: 3%;
  font-size: 2.25vw;
}

header nav ul li {
  padding: 0 45px;
}

span {
  cursor: pointer;
}

/* About Section */
#bio {
  margin-top: 1%;
  font-size: 2vw;
  text-align: center;
}

.Self {
  width: 15%;
  height: 35%;
  display: block;
  margin: auto;
  margin-top: 45px;
}

/* Work */
.project-title {
  text-align: center;
  font-size: 2.5vw;
  margin-bottom: 1%;
}

.project-title span {
  font-size: 1.5vw;
  cursor: text
}

.project-description {
  text-align: center;
  font-size: 1.5vw;
  margin-top: .5%;
  margin-bottom: 2%;
}

#work section a {
  text-decoration: none;
  margin: 10px;
  height: 200px;
  width: 100%;
}

.project-img {
  display: block;
  margin: auto;
  width: 45vw;
  height: 30vh;
}

/* Contact */
.sub-title {
  font-size: 3vw;
  text-align: center;
  margin-bottom: 1%;
}

.info {
  display: flex;
  flex-direction: column;
  margin-top: 1%;
}

.info li {
  list-style-type: none;
  margin: 1%;
  font-size: 2vw;
}

/* Resume */

.download-div {
  text-align: center;
  margin-bottom: 1%;
}

.download {
  text-decoration: none;
  font-size: 1.5vw;
}

.resume {
  display: block;
  margin: auto;
  transition: 0.5s all ease-in-out;
}

.resume:hover {
  transform: scale(1.4)
}

/* Background */

.rain {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.rain.back-row {
  display: none;
  z-index: -2;
  bottom: 60px;
  opacity: 0.5;
}

.drop {
  position: absolute;
  bottom: 100%;
  width: 10px;
  height: 100px;
  pointer-events: none;
  animation: drop 10s linear infinite;
}
@keyframes drop {
  0% {
      transform: translateY(0vh);
  }
  75% {
      transform: translateY(90vh);
  }
  100% {
      transform: translateY(90vh);
  }
}

.stem {
    width: .5px;
    height: 40%;
    margin-left: 7px;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.25));
    animation: stem 0.5s linear infinite;
}

@keyframes stem {
  0% {
      opacity: 1;
  }
  65% {
      opacity: 1;
  }
  75% {
      opacity: 0;
  }
  100% {
      opacity: 0;
  }
}

.splat {
  width: 15px;
  height: 10px;
  border-top: 2px dotted rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  opacity: 1;
  transform: scale(0);
  animation: splat 0.5s linear infinite;
  display: none;
}

@keyframes splat {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  80% {
    opacity: 1;
    transform: scale(0);
  }
  90% {
    opacity: 0.5;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1.5);
  }
}